import { Card, CardActionArea, CardContent, CardMedia, Grid, Typography } from '@mui/material';
import NoImage from '../Image/NoImage.jpg'
import { Link } from "react-router-dom"


function LeagueItem({idLeague, nameLeague, name, flagLeague}){

    const leagueLink = `/leaque/${idLeague}/matches`

    if(!flagLeague){
        flagLeague=NoImage
    }

    return(
        <Grid item xs={2} margin={3} my={5}> {/*  item -   grid  teamsList | xs-  - | my -   */}
            <Card> {/*   */}
                <CardActionArea component={Link} to={leagueLink}>
                    <CardMedia //   
                        component="img"
                        alt=" "
                        src={flagLeague}
                        sx={{ height: 170, pb: "10px" }}
                    />

<                   Typography sx={{ fontWeight: "bold" }} variant="h6">
                        {nameLeague}
                    </Typography>
                        
                    <Typography variant="overline" color="gray">
                        {name}
                    </Typography> 

                </CardActionArea>
            </Card>
        </Grid>
    )

}

export default LeagueItem;